import { ANDROID, IOS, LOGO } from '@/config/image.js';
var _self;
export default {
	data() {
		return {
			shoeOne:false,
			shoeTwo:false,
			img:[LOGO,ANDROID,IOS]
		}
	},
	onLoad() {
		_self=this

	},
	methods: {
		submitTest(){
			_self.shoeOne=!this.shoeOne
			_self.shoeTwo = false
		},
		submitApply(){
			_self.shoeTwo=!this.shoeTwo
			_self.shoeOne = false
		}

	}
}
